import React from 'react'
import './Card.css'

const Card = ({ source, text, fcolor, scolor }) => {
    return (
        <div className="card" style={{
            background:
                `
                linear-gradient(to left, ${fcolor} , ${scolor});
            `
        }} >
            <img src={source} alt="lang.svg" />
            <h4>{text}</h4>
        </div>
    )
}

// Card.propTypes = {
//     // source: "string",
//     text: "string",
//     fcolor: "string",
//     scolor: "string",
// }

export default Card;